import { Dropbox } from 'dropbox';
import {createInstance} from 'localforage';
import 'whatwg-fetch';

const store = createInstance({name: 'store-dropbox'})

const localClientId = 'jh9cnt92wi5e5my';
const prodClientId = 'li12ai4q2cs3dkc';

export const isLocalMode = window.location.host !== 'life.rasmuskl.dk';

const clientId = isLocalMode ? localClientId : prodClientId;

tryStoreAccessKey();

const localFetch = window.fetch;

export function signIn() {
    window.location.assign(`https://www.dropbox.com/oauth2/authorize?client_id=${clientId}&response_type=token&redirect_uri=${window.location.toString()}`);
}

export async function isSignedIn() {
    return !!(await store.getItem('token'));
}

interface DropboxSnapshot {
    cursor: string|null;
    files: Set<string>;
}

async function getStateSnapshot() {
    const snapshot = await store.getItem('snapshot') as DropboxSnapshot|null;
    return snapshot || { cursor: null, files: new Set<string>() };
}

async function setStateSnapshot(snapshot: DropboxSnapshot|null) {
    if (!snapshot) {
        await store.removeItem('snapshot');
    } else {
        await store.setItem('snapshot', snapshot);
    }
}
export async function disconnect() {
    await store.removeItem('token');
}

export async function refreshDropbox() {
    await setStateSnapshot(null);
}

export async function listFiles(useSnapshot = true) {
    if(!await isSignedIn()) {
        throw new Error('Not authenticated');
    }

    const dropbox = new Dropbox({ accessToken: await getAccessToken(), fetch: localFetch });
    let snapshot: DropboxSnapshot = { cursor: null, files: new Set<string>() };

    if (useSnapshot) {
        snapshot = await getStateSnapshot();
    }

    let hasMore = true;

    while(hasMore) {
        if (snapshot.cursor) {
            const fileListResult = await dropbox.filesListFolderContinue({ cursor: snapshot.cursor });
            const results = fileListResult.entries.filter(e => e['.tag'] === 'file').map(e => e.path_lower?.substring(1)).filter(p => p) as string[];
            snapshot.cursor = fileListResult.cursor;
            for(const file of results) {
                snapshot.files.add(file);
            }
            hasMore = fileListResult.has_more;
        } else {
            const fileListResult = await dropbox.filesListFolder({path: '', recursive: true});
            const results = fileListResult.entries.filter(e => e['.tag'] === 'file').map(e => e.path_lower?.substring(1)).filter(p => p) as string[];
            snapshot.cursor = fileListResult.cursor;    
            for(const file of results) {
                snapshot.files.add(file);
            }
            hasMore = fileListResult.has_more;
        }

        await setStateSnapshot(snapshot);
    }

    return snapshot.files;
}

export function getAccessTokenFromUrl() {
    const results = parseAuthFromHash(window.location.hash);
    return results.access_token;
}

export async function tryStoreAccessKey() {
    const accessToken = getAccessTokenFromUrl();

    if (accessToken) {
        await store.setItem('token', accessToken);
    }
}

export async function uploadFile(path: string, contents: string) {
    if(!await isSignedIn()) {
        throw new Error('Not authenticated');
    }

    const dropbox = new Dropbox({ accessToken: await getAccessToken(), fetch: localFetch });
    await dropbox.filesUpload({
        path: `/${path}`,
        contents
    });
}

export async function getFile<T>(path: string) {
    if(!await isSignedIn()) {
        throw new Error('Not authenticated');
    }

    const dropbox = new Dropbox({ accessToken: await getAccessToken(), fetch: localFetch });
    const response = await dropbox.filesDownload({path: `/${path}` })
    return new Promise<T>((resolve) => {
        const blob: Blob = (response as any).fileBlob;
        const reader = new FileReader();
        reader.addEventListener("loadend", function() {
            const json = reader.result as string;
            resolve(JSON.parse(json) as T);
        });
        reader.readAsText(blob);
    });
}


async function getAccessToken() {
    return await store.getItem('token') as string;
}

function parseAuthFromHash(str: string) {
    const ret: { [k: string]: string[] | string } = {};

    if (typeof str !== 'string') {
        return ret;
    }

    str = str.trim().replace(/^(\?|#|&)/, '');

    if (!str) {
        return ret;
    }

    str.split('&').forEach(function (param) {
        const parts = param.replace(/\+/g, ' ').split('=');
        let key = parts.shift();
        let val = parts.length > 0 ? parts.join('=') : null;

        key = decodeURIComponent(key!);
        val = val ? decodeURIComponent(val) : null;

        var retVal = ret[key];
        if (ret[key] === undefined) {
            ret[key] = val!;
        } else if (Array.isArray(retVal)) {
            retVal.push(val!);
        } else {
            ret[key] = [ret[key] as string, val!];
        }
    });

    return ret;
}