export interface ServerEntity {
    id: string;
    versionId: string;
    data: string;
    // encryption: string;
    parentVersionId1: string|null;
    parentVersionId2: string|null;
    type: EntityType;
    createdAt: string;
    updatedAt: string;
}

export interface ServerEntityEncryption {
    key: string;
    fingerprint: string;
    iv: string;
}

type Omit<T, K extends keyof T> = Pick<T, Exclude<keyof T, K>>

class LocalForageStore<T extends Entity> implements Store<T> {
    store: LocalForage;
    type: EntityType;
    changeListener: EventListener<T>;

    constructor(type: EntityType) {
        this.type = type;
        this.store = createInstance({
            name: `store-${type}`
        });
        this.changeListener = new EventListener<T>();
    }

    createUnsaved(entityProps: Omit<T, keyof Entity>): T {
        const now = new Date().toISOString();
        let entity = {
            id: this.uuid(),
            versionId: this.uuid(),
            type: this.type,
            parentVersionIds: [] as string[],
            createdAt: now,
            updatedAt: now,
            ...entityProps as any,
        } as T;
        return entity;
    }

    async create(entityProps: Omit<T, keyof Entity>): Promise<T> {
        const now = new Date().toISOString();
        let entity = {
            id: this.uuid(),
            versionId: this.uuid(),
            type: this.type,
            parentVersionIds: [] as string[],
            createdAt: now,
            updatedAt: now,
            ...entityProps as any,
        } as T;
        await this.store.setItem(this.getKey(entity), entity);
        this.changeListener && this.changeListener.trigger(entity);
        return entity;
    }

    save(entity: T): void {
        if (!entity.id) {
            entity.id = this.uuid();
        }

        if (entity.type !== this.type) {
            throw new Error(`Invalid type: ${entity.type} - expected ${this.type}`);
        }

        const now = new Date().toISOString();

        if (!entity.createdAt) {
            entity.createdAt = now;
        }

        entity.updatedAt = now;

        if(entity.versionId) {
            entity.parentVersionId1 = entity.versionId;
        }

        entity.versionId = this.uuid();
        this.store.setItem(this.getKey(entity), entity);
        this.changeListener && this.changeListener.trigger(entity);
    }

    import(entity: T, suppressChangeListener = false) {
        if (entity.type !== this.type) {
            throw new Error(`Invalid type ${entity.type} - expected ${this.type}`);
        }

        this.store.setItem(this.getKey(entity), entity);
        !suppressChangeListener && this.changeListener && this.changeListener.trigger(entity);
    }

    async get(id: string, versionId?: string): Promise<T|null> {
        if (versionId === undefined) {
            const leafNodes = await this.getLeafNodes(`${id}/`);

            if (leafNodes.length > 1) {
                console.warn(`Found ${leafNodes.length} leaf nodes for entity ${this.type}/${id} - expected 1: ${leafNodes.map(x => x.versionId)}`);

                for(const node of leafNodes) {
                    console.log('leaf node', node);
                }
                
                // throw `Found ${leafNodes.length} leaf nodes for entity ${this.type}/${id} - expected 1: ${leafNodes.map(x => x.versionId)}`;
                return _.orderBy(leafNodes, x => x.updatedAt, 'desc')[0];
            }

            if (leafNodes.length === 0) {
                return null;
            }

            return leafNodes[0];
        } else {
            const keys = await this.getKeysPrefixed(`${id}/${versionId}`);
            return await this.store.getItem(keys[0]) as T;
        }
    }

    async getAll(): Promise<T[]> {
        return await this.getLeafNodes();
    }

    async getKeysPrefixed(prefix: string) {
        const keys = await this.store.keys();
        return keys.filter(k => k.startsWith(prefix));
    }

    async getSyncKeys(): Promise<string[]> {
        const keys = await this.store.keys();
        return keys.map(x => `${this.type}/${x.split('/')[0]}/${x.split('/')[1]}`)
    }

    private async getLeafNodes(prefix = '') {
        const keys = await this.getKeysPrefixed(prefix);

        const objs = keys.map(k => {
            const parts = k.split('/');

            let parentVersionId1 = null;
            let parentVersionId2 = null;

            if (parts[2].length > 0) {
                const parentVersionIds = parts[2].split('+');

                if (parentVersionIds.length > 2) {
                    throw new Error("ParentVersionIds length > 2");
                }

                parentVersionId1 = parentVersionIds[0];
                parentVersionId2 = parentVersionIds.length > 1 ? parentVersionIds[1] : null;
            }

            return {
                id: parts[0],
                versionId: parts[1],
                parentVersionId1: parentVersionId1,
                parentVersionId2: parentVersionId2,
            }
        });

        const groups = _.groupBy(objs, o => o.id);

        const leafNodeArrays = Object.keys(groups).map(k => {
            const group = groups[k];
            const parents: { [key: string]: boolean } = {};

            for(const o of group) {
                if (o.parentVersionId1) {
                    parents[o.parentVersionId1] = true;
                }
                if (o.parentVersionId2) {
                    parents[o.parentVersionId2] = true;
                }
            }

            const leafNodes = group.filter(o => {
                const isLeaf = !parents[o.versionId];
                return isLeaf;
            });

            return leafNodes;
        });

        const result: T[] = [];

        for(let leafNodes of leafNodeArrays) {
            const leafObjs: T[] = [];
            for(let node of leafNodes) {
                leafObjs.push(await (this.store.getItem(this.getKey(node))) as T);
            }

            if(leafObjs.length !== 1) {
                console.warn(`Found ${leafObjs.length} leaf nodes for entity ${this.type}/${leafNodes[0].id} - expected 1: ${leafObjs.map(x => x.versionId)}`);
    
                for(const node of leafObjs) {
                    console.log('leaf node', node);
                }
    
                result.push(_.orderBy(leafObjs, x => x.updatedAt, 'desc')[0])
            } else {
                result.push(leafObjs[0]);
            }
        }

        return result;
    }

    registerChangeListener(listener: (status: T) => void) {
        this.changeListener.register(listener)
    }

    dropInstance() {
        this.store.dropInstance();
    }

    private getKey(entity: { id: string, versionId: string, parentVersionId1: string|null, parentVersionId2: string|null }) {
        let parentVersionIds = [];

        if (entity.parentVersionId1) {
            parentVersionIds.push(entity.parentVersionId1);
        }

        if (entity.parentVersionId2) {
            parentVersionIds.push(entity.parentVersionId2);
        }


        return `${entity.id}/${entity.versionId}/${parentVersionIds.join('+')}`;
    }

    private uuid() {
        return 'xxxxxxxx-xxxx-xxxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, function(c) {
            const r = Math.random() * 16 | 0, v = c === 'x' ? r : ((r & 0x3) | 0x8);
            return v.toString(16);
        });
    }
}

const contexts = new LocalForageStore<Context>("context");
const tasks = new LocalForageStore<Task>("task");
const notes = new LocalForageStore<Note>("note");
const projects = new LocalForageStore<Project>("project");
const people = new LocalForageStore<Person>("person");

async function getSyncKeys() {
    const keys: string[] = [
        ...(await contexts.getSyncKeys()),
        ...(await tasks.getSyncKeys()),
        ...(await notes.getSyncKeys()),
        ...(await projects.getSyncKeys()),
        ...(await people.getSyncKeys()),
    ];

    return keys;
}

async function getBySyncKey(syncKey: string): Promise<Entity|null> {
    const [type, id, versionId] = syncKey.split('/');

    switch(type) {
        case 'context':
            return contexts.get(id, versionId);
        case 'task':
            return tasks.get(id, versionId);
        case 'note':
            return notes.get(id, versionId);
        case 'project':
            return projects.get(id, versionId);
        case 'person':
            return people.get(id, versionId);
        default:
            throw new Error('Unknown type: ' + type);
    }
}

async function importEntites(entities: Entity[]) {
    for(const entity of entities) {
        switch(entity.type) {
            case 'context':
                contexts.import(entity as Context);
                break;
            case 'task':
                tasks.import(entity as Task);
                break;
            case 'note':
                notes.import(entity as Note);
                break;
            case 'project':
                projects.import(entity as Project);
                break;
            case 'person':
                people.import(entity as Person);
                break;
            default:
                throw new Error('Unknown type: ' + entity.type);
        }
    }
}

async function importServerEntites(serverEntities: ServerEntity[]) {
    for(const serverEntity of serverEntities) {
        // const encryption = JSON.parse(serverEntity.encryption) as ServerEntityEncryption;
        // const unencryptedData = await unwrapAndDecrypt(encryption.fingerprint, encryption.key, serverEntity.data, encryption.iv);
        const unencryptedData = serverEntity.data;
        let entity = JSON.parse(unencryptedData) as Entity;

        entity.id = serverEntity.id;
        entity.versionId = serverEntity.versionId;
        entity.type = serverEntity.type;
        entity.parentVersionId1 = serverEntity.parentVersionId1;
        entity.parentVersionId2 = serverEntity.parentVersionId2;
        entity.createdAt = serverEntity.createdAt;
        entity.updatedAt = serverEntity.updatedAt;

        switch(serverEntity.type) {
            case 'context':
                contexts.import(entity as Context, true);
                break;
            case 'task':
                tasks.import(entity as Task, true);
                break;
            case 'note':
                notes.import(entity as Note, true);
                break;
            case 'project':
                projects.import(entity as Project, true);
                break;
            case 'person':
                people.import(entity as Person, true);
                break;
            default:
                throw new Error('Unknown type: ' + entity.type);
        }
    }
}

async function exportServerEntities(syncKeys: string[]): Promise<ServerEntity[]> {
    const result: ServerEntity[] = [];

    for (const syncKey of syncKeys) {
        const entity = await getBySyncKey(syncKey);

        if (entity === null) {
            console.warn("Entity with key not found: " + syncKey)
            continue;
        }

        const entityData = {...entity};

        delete entityData.createdAt;
        delete entityData.updatedAt;
        delete entityData.id;
        delete entityData.versionId;
        delete entityData.type;
        delete entityData.parentVersionId1;
        delete entityData.parentVersionId2;

        const unencryptedData = JSON.stringify(entityData);
        // const encryptionResult = await encryptAndWrap((await getCurrentKey()).fingerprint, unencryptedData);
        // const encryption: ServerEntityEncryption = { 
        //     fingerprint: encryptionResult.fingerprint, 
        //     key: encryptionResult.key,
        //     iv: encryptionResult.iv
        // };
        // const encryptionJson = JSON.stringify(encryption);

        const serverEntity: ServerEntity = {
            id: entity.id,
            versionId: entity.versionId,
            type: entity.type,
            parentVersionId1: entity.parentVersionId1,
            parentVersionId2: entity.parentVersionId2,
            createdAt: entity.createdAt,
            updatedAt: entity.updatedAt,
            data: unencryptedData,
            // encryption: encryptionJson
        };

        result.push(serverEntity);
    }

    return result;
}

export const db = {
    contexts,
    tasks,
    notes,
    projects,
    people,
    getSyncKeys,
    importEntites,
    importServerEntites,
    exportServerEntities
};