import * as React from 'react';
import { useData } from '../model';
import { db } from '../db/db';
import { Person } from '../db/person';
import { NavLink } from 'react-router-dom';
import { MarkedViewer } from './MarkedViewer';
import { Task } from '../db/task';
import { HasNote } from '../db/note';
import { TreeViewer } from './TreeViewer';

export const EmbeddedNoteEdit: React.FC<{ targetEntity: Person|Task|null }> = (props) => {
    const {data} = useData();

    if (!props.targetEntity) {
        return null;
    }

    const {noteId} = props.targetEntity;

    const addNode = async () => {
        if (!props.targetEntity) {
            return;
        }

        const note = await db.notes.create({
            title: '',
            content: '',
            tags: '',
            contextId: null,
            noteFor: {
                type: props.targetEntity.type,
                id: props.targetEntity.id,
            },
            mode: 'tree',
            dailyDate: null
        });

        if (props.targetEntity.type === 'person') {
            props.targetEntity.noteId = note.id;
            await db.people.save(props.targetEntity);
        }

        if (props.targetEntity.type === 'task') {
            props.targetEntity.noteId = note.id;
            await db.tasks.save(props.targetEntity);
        }
    };

    if (!noteId) {
        return (
            <div>
                <div className="button" onClick={addNode}>Add note</div>
            </div>
        );
    }

    const note = data.notes.find(x => x.id === noteId);

    if (!note) {
        return null;
    }

    return (
        <div>
            <NavLink to={`/notes/${note.id}/edit?source=${encodeURIComponent(`${window.location.pathname}`)}`} className="button">Edit note</NavLink>
            {!note.mode || note.mode === 'markdown' ? (
                <MarkedViewer markdown={note.content} contextId={note.contextId} />
            ) : (
                <TreeViewer json={note.content} contextId={note.contextId} />
            )}
        </div>
    )
};

export const EmbeddedNoteShow: React.FC<{ targetEntity: HasNote|null }> = (props) => {
    const {data} = useData();

    if (!props.targetEntity) {
        return null;
    }

    const {noteId} = props.targetEntity;
    if (!noteId) {
        return null;
    }

    const note = data.notes.find(x => x.id === noteId);

    if (!note) {
        return null;
    }

    return (
        <div>
            {!note.mode || note.mode === 'markdown' ? (
                <MarkedViewer markdown={note.content} contextId={note.contextId} />
            ) : (
                <TreeViewer json={note.content} contextId={note.contextId} />
            )}
        </div>
    )
};