import * as React from 'react';

export function ServiceWorkerUpdate() {
    const [waitingServiceWorker, setWaitingServiceWorker] = React.useState<ServiceWorker|null>(null);

    React.useEffect(() => {
        if (!navigator.serviceWorker) {
            return;
        }

        async function componentDidMount() {
            const registration = await navigator.serviceWorker.ready;
            setWaitingServiceWorker(registration.waiting);
            registration.addEventListener('updatefound', () => {
                registration.installing?.addEventListener('statechange', (event: any) => {
                    if (event.target.state === 'installed') {
                        setWaitingServiceWorker(event.target);
                    }
                })
            });
            registration.update();
        }
        
        componentDidMount();

        setInterval(async () => {
            const registration = await navigator.serviceWorker.ready;
            registration.update();
        }, 60 * 60 * 1000)
    }, [])

    if (!waitingServiceWorker) {
        return null;
    }

    const updateServiceWorker = () => {
        if (!navigator.serviceWorker) {
            return;
        }

        waitingServiceWorker.addEventListener('statechange', (event: any) => {
            if (event.target.state === 'activated') {
                window.location.reload()
              }
        })
        waitingServiceWorker.postMessage({ type: 'SKIP_WAITING' })
    };

    return (
        <div className="button" onClick={updateServiceWorker}>Update</div>
    );
}