declare var self: ServiceWorkerGlobalScope;

const isLocalhost = Boolean(window.location.hostname === 'localhost' || window.location.hostname === '[::1]' || window.location.hostname.match(/^127(?:\.(?:25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)){3}$/));

type Config = {
    onSuccess?: (registration: ServiceWorkerRegistration) => void;
    onUpdate?: (registration: ServiceWorkerRegistration) => void;
};

export async function register(config?: Config) {
    if (process.env.NODE_ENV === 'production' && 'serviceWorker' in navigator) {
        
        // The URL constructor is available in all browsers that support SW.
        const publicUrl = new URL(process.env.PUBLIC_URL, window.location.href);
        
        if (publicUrl.origin !== window.location.origin) {
            // Our service worker won't work if PUBLIC_URL is on a different origin
            // from what our page is served on. This might happen if a CDN is used to
            // serve assets; see https://github.com/facebook/create-react-app/issues/2374
            return;
        }
        
        window.addEventListener('load', async () => {
            const swUrl = `${process.env.PUBLIC_URL}/service-worker.js`;
            
            if (isLocalhost) {
                // This is running on localhost. Let's check if a service worker still exists or not.
                checkValidServiceWorker(swUrl, config);
                
                // Add some additional logging to localhost, pointing developers to the
                // service worker/PWA documentation.
                await navigator.serviceWorker.ready;
                console.log('This web app is being served cache-first by a service worker.'
                );
            } else {
                // Is not localhost. Just register service worker
                await registerValidSW(swUrl, config);
            }
        });

        self.addEventListener('message', event => {
            if (event.data && event.data.type === 'SKIP_WAITING') {
                self.skipWaiting();
              }
        });
    }
}

async function registerValidSW(swUrl: string, config?: Config) {
    const registration = await navigator.serviceWorker.register(swUrl);
    
    registration.onupdatefound = () => {
        const installingWorker = registration.installing;
        
        if (installingWorker == null) {
            return;
        }
        
        installingWorker.onstatechange = () => {
            if (installingWorker.state === 'installed') {
                if (navigator.serviceWorker.controller) {
                    // At this point, the updated precached content has been fetched,
                    // but the previous service worker will still serve the older
                    // content until all client tabs are closed.
                    console.log('New content is available and will be used when all tabs for this page are closed.');
                    
                    // Execute callback
                    if (config && config.onUpdate) {
                        config.onUpdate(registration);
                    }
                } else {
                    // At this point, everything has been precached.
                    // It's the perfect time to display a
                    // "Content is cached for offline use." message.
                    console.log('Content is cached for offline use.');
                    
                    // Execute callback
                    if (config && config.onSuccess) {
                        config.onSuccess(registration);
                    }
                }
            }
        };
    };
}

async function checkValidServiceWorker(swUrl: string, config?: Config) {
    // Check if the service worker can be found. If it can't reload the page.
    const response = await fetch(swUrl, {
        headers: { 'Service-Worker': 'script' }
    });
    
    // Ensure service worker exists, and that we really are getting a JS file.
    const contentType = response.headers.get('content-type');
    if (response.status === 404 || (contentType != null && contentType.indexOf('javascript') === -1)) {
        // No service worker found. Probably a different app. Reload the page.
        const registration = await navigator.serviceWorker.ready;
        await registration.unregister()
        window.location.reload();
    } else {
        // Service worker found. Proceed as normal.
        registerValidSW(swUrl, config);
    }
}

export async function unregister() {
    if ('serviceWorker' in navigator) {
        const registration = await navigator.serviceWorker.ready;
        registration.unregister();
    }
}
