import {createInstance} from 'localforage';
import * as md5 from 'js-md5';

const lifeKeyStore = createInstance({ name: 'lifekeystore' });
const privateKeyStore = createInstance({ name: 'privatekeystore' });

export interface LifeKey {
    fingerprint: string;
    publicKey: string;
    createdAt: string;
}

const rsaSettings = {
    name: "RSA-OAEP",
    modulusLength: 2048,
    publicExponent: new Uint8Array([0x01, 0x00, 0x01]),
    hash: {name: "SHA-256"},
};

const aesSettings = { name: 'AES-GCM', length: 256 };

export async function generateRsaKey() {
    try {
        const keyPair = await crypto.subtle.generateKey(
            rsaSettings,
            true,
            ['encrypt', 'decrypt']
        );

        const rawPrivateKey = await crypto.subtle.exportKey('pkcs8', keyPair.privateKey);
        const privateKeyBase64 = arrayBufferToBase64(rawPrivateKey);

        const rawPublicKey = await crypto.subtle.exportKey('spki', keyPair.publicKey);
        const publicKeyBase64 = arrayBufferToBase64(rawPublicKey);
        const publicKeyFingerprint = md5.hex(rawPublicKey);

        return { fingerprint: publicKeyFingerprint, privateKey: privateKeyBase64, publicKey: publicKeyBase64 };
    } catch (error) {
        console.error('generateKey', error, error.message)
        throw error;
    }
}

export async function derivePublicRsaKey(privateKey: CryptoKey) {
    const jwk = await crypto.subtle.exportKey("jwk", privateKey);

    delete jwk.d;
    delete jwk.dp;
    delete jwk.dq;
    delete jwk.q;
    delete jwk.qi;
    jwk.key_ops = ["encrypt", "wrapKey"];
    
    const publicKey = await crypto.subtle.importKey("jwk", jwk, rsaSettings, true, ["encrypt", "wrapKey"]);
    return publicKey;
}

export async function importLifeKey(privateKeyBase64: string) {
    const importedPrivateKey = base64ToArrayBuffer(privateKeyBase64);
    const extractablePrivateKey = await crypto.subtle.importKey('pkcs8', importedPrivateKey, rsaSettings, true, ['decrypt', 'unwrapKey']);
    
    const publicKey = await derivePublicRsaKey(extractablePrivateKey);
    const rawPublicKey = await crypto.subtle.exportKey('spki', publicKey);
    const publicKeyBase64 = arrayBufferToBase64(rawPublicKey);
    const publicKeyFingerprint = md5.hex(rawPublicKey);

    const privateKey = await crypto.subtle.importKey('pkcs8', importedPrivateKey, rsaSettings, false, ['decrypt', 'unwrapKey']);
    privateKeyStore.setItem(publicKeyFingerprint, privateKey);

    const lifeKey: LifeKey = {
        fingerprint: publicKeyFingerprint,
        publicKey: publicKeyBase64,
        createdAt: new Date().toISOString(),
    };

    lifeKeyStore.setItem(lifeKey.fingerprint, lifeKey);
}

export async function encryptAndWrap(fingerprint: string, data: string) {
    try {
        const lifeKey = await lifeKeyStore.getItem(fingerprint) as LifeKey;
        if (!lifeKey) {
            throw new Error(`Key with fingerprint ${fingerprint} not found`);
        }        
        const importedPublicKeyBuffer = base64ToArrayBuffer(lifeKey.publicKey);
        const publicKey = await crypto.subtle.importKey('spki', importedPublicKeyBuffer, rsaSettings, true, ['encrypt', 'wrapKey']);
    
        const aesKey = await generateAesKey();
        const iv = crypto.getRandomValues(new Uint8Array(12));
    
        const encryptedData = await crypto.subtle.encrypt({ 
            name: 'AES-GCM', 
            length: 256,
            iv
        }, aesKey, strToArrayBuffer(data));
    
        const encryptedDataBase64 = arrayBufferToBase64(encryptedData);
    
        const wrappedAesKey = await crypto.subtle.wrapKey('raw', aesKey, publicKey, rsaSettings);
        const wrappedAesKeyBase64 = arrayBufferToBase64(wrappedAesKey);
    
        const ivBase64 = arrayBufferToBase64(iv);
    
        const result = {
            payload: encryptedDataBase64,
            key: wrappedAesKeyBase64,
            fingerprint: fingerprint,
            iv: ivBase64
        };
    
        return result;
    } catch (error) {
        console.error('encryptAndWrap', error, error.message)
        throw error;
    }
}

export async function unwrapAndDecrypt(fingerprint: string, wrappedKeyBase64: string, dataBase64: string, ivBase64: string) {
    try {
        const privateKey = await privateKeyStore.getItem(fingerprint) as CryptoKey;
        if (!privateKey) {
            throw new Error(`Key with fingerprint ${fingerprint} not found`);
        }

        const aesKey = await crypto.subtle.unwrapKey('raw', base64ToArrayBuffer(wrappedKeyBase64), privateKey, rsaSettings, aesSettings, false, ['decrypt'])
        const unencryptedDataBuffer = await crypto.subtle.decrypt({ 
            name: 'AES-GCM', 
            length: 256,
            iv: base64ToArrayBuffer(ivBase64)
        }, aesKey, base64ToArrayBuffer(dataBase64));
        return arrayBufferToString(unencryptedDataBuffer);
    } catch (error) {
        console.error('unwrapAndDecrypt', error, error.message)
        throw error;
    }
}

export async function generateAesKey(): Promise<any> {
    try {
        const key = await crypto.subtle.generateKey(aesSettings, true, ['encrypt', 'decrypt']);
        return key;
    } catch (error) {
        console.error('generateKey', error, error.message)
        throw error;
    }
}

export async function getCurrentKey() {
    const keys = await lifeKeyStore.keys();

    if (keys.length === 0) {
        throw new Error('No key found');
    }

    if (keys.length > 1) {
        throw new Error('More than 1 key found!');
    }

    return (await lifeKeyStore.getItem(keys[0])) as LifeKey;
}


function arrayBufferToBase64( buffer: ArrayBuffer ) {
    var binary = '';
    var bytes = new Uint8Array(buffer);
    var len = bytes.byteLength;
    for (var i = 0; i < len; i++) {
        binary += String.fromCharCode(bytes[i]);
    }
    return window.btoa( binary );
}

function base64ToArrayBuffer(base64: string) {
    var binary_string =  window.atob(base64);
    var len = binary_string.length;
    var bytes = new Uint8Array(len);
    for (var i = 0; i < len; i++)        {
        bytes[i] = binary_string.charCodeAt(i);
    }
    return bytes.buffer;
}

