import * as React from 'react';
import { NavLink } from 'react-router-dom';
import { Data } from '../model';
import { ToolSection } from '../framework/ToolSection';
import { Task, TaskState } from '../db/task';
import { db } from '../db/db';
import { ProjectState } from '../db/project';
import moment from 'moment';
import * as styles from '../layout/styles';
import { EmbeddedNoteEdit } from '../components/EmbeddedNote';
import { cx } from 'emotion';

export class ShowTaskPage extends React.Component<{ data: Data, id: string, refreshData(): void }, { title: string, source: string }> {

    constructor(props: any) {
        super(props);

        this.state = {
            title: '',
            source: '/tasks'
        }
    }

    componentDidMount() {
        const urlParams = new URLSearchParams(window.location.search);

        let task = this.props.data.tasks.find(x => x.id === this.props.id);
        this.setState({ title: task!.title }, () => {
            if (urlParams.has('source')) {
                this.setState({ source: decodeURIComponent(urlParams.get('source')!)});
            }
        });
    }

    async setContext(task: Task, event: React.ChangeEvent<HTMLSelectElement>) {
        let contextId: string|null = event.target.value;

        if (contextId === '') {
            contextId = null;
        }

        task.contextId = contextId;
        await db.tasks.save(task);
        this.props.refreshData();
    }

    async setProject(task: Task, event: React.ChangeEvent<HTMLSelectElement>) {
        let projectId: string|null = event.target.value;

        if (projectId === '') {
            projectId = null;
        }

        task.projectId = projectId;
        await db.tasks.save(task);
        this.props.refreshData();
    }

    async setDefer(task: Task, event: React.ChangeEvent<HTMLInputElement>) {
        let value: string|null = event.target.value;

        if (value === '') {
            task.deferredUntil = null;
        } else {
            task.deferredUntil = new Date(value).toISOString();
        }

        await db.tasks.save(task);
        this.props.refreshData();
    }

    async onKeyDown(e: React.KeyboardEvent<HTMLInputElement>) {
        if (e.key === 'Enter') {
            let task = this.props.data.tasks.find(x => x.id === this.props.id);

            if (!task) {
                return;
            }

            if (task.title !== this.state.title) {
                task.title = this.state.title;
                await db.tasks.save(task);
                this.props.refreshData();
            }
        }
    }

    async setTaskState(state: TaskState) {
        const task = this.props.data.tasks.find(x => x.id === this.props.id);

        if (!task) {
            return;
        }

        if (task.state === state) {
            return;
        }

        task.state = state;
        task.stateChangedAt = new Date().toISOString();
        await db.tasks.save(task);
        this.props.refreshData();
    }

    render() {
        let task = this.props.data.tasks.find(x => x.id === this.props.id);

        if (!task) {
            return <div>Task not found.</div>
        }

        let isDirty = false;

        if (task.title !== this.state.title) {
            isDirty = true;
        }

        return (
            <>
                <ToolSection>
                    <NavLink to={this.state.source} className="button">Back</NavLink>
                    {/* <NavLink to={`/tasks/${task.id}/edit`} className="button">Edit</NavLink> */}
                </ToolSection>

                <ToolSection>
                    <div>Task</div>
                    <input type="text" className="input is-large is-wide" style={{border: isDirty ? '2px solid orange' : undefined}} value={this.state.title} onKeyDown={e => this.onKeyDown(e)} onChange={e => this.setState({ title: e.target.value })} />

                    <div>State</div>
                    <div className={cx(styles.horizontalSpace('.5rem'))}>
                        <div className={cx('button', task.state !== 'active' ? 'inactive' : null)} onClick={() => this.setTaskState('active')}>Active</div>
                        <div className={cx('button', task.state !== 'incubated' ? 'inactive' : null)} onClick={() => this.setTaskState('incubated')}>Incubated</div>
                        <div className={cx('button', task.state !== 'completed' ? 'inactive' : null)} onClick={() => this.setTaskState('completed')}>Completed</div>
                    </div>

                    <div>Context</div>
                    <select className="input is-large" value={task.contextId || ''} onChange={e => this.setContext(task!, e)}>
                        <option value="">Not set</option>
                        {this.props.data.contexts.map(c => (
                            <option key={c.id} value={c.id}>{c.title}</option>
                        ))}
                    </select>

                    <div>Project</div>
                    <select className="input is-large" value={task.projectId || ''} onChange={e => this.setProject(task!, e)}>
                        <option value="">Not set</option>
                        {this.props.data.projects.filter(p => p.state === ProjectState.Active || p.id === task!.projectId).map(c => (
                            <option key={c.id} value={c.id}>{c.title}</option>
                        ))}
                    </select>

                    <div>Defer</div>
                    <input type="date" className="input is-large" value={(task.deferredUntil && moment(task.deferredUntil).format('YYYY-MM-DD')) || ''} onChange={e => this.setDefer(task!, e)} />
                </ToolSection>

                <ToolSection>
                    <EmbeddedNoteEdit targetEntity={task} {...this.props} />
                </ToolSection>
            </>
        );
    }
}