import * as React from 'react';

export class ServiceWorkerInstall extends React.Component<{ }, { installEvent: any }> {

    constructor(props: any) {
        super(props);
        this.state = {
            installEvent: null
        };
    }

    componentDidMount() {
        window.addEventListener('beforeinstallprompt', (e: any) => {
            // Prevent Chrome 67 and earlier from automatically showing the prompt
            e.preventDefault();
            this.setState({ installEvent: e });
        });
    }

    onInstallClick() {
        if (this.state.installEvent === null) {
            return;
        }
        
        this.state.installEvent.prompt();
    
        // Wait for the user to respond to the prompt
        this.state.installEvent.userChoice.then((choiceResult: any) => {
            if (choiceResult.outcome === 'accepted') {
                console.log('User accepted the A2HS prompt');
            } else {
                console.log('User dismissed the A2HS prompt');
            }
        });
    }

    render() {
        if (!this.state.installEvent) {
            return null;
        }

        return (
            <div className="button" onClick={() => this.onInstallClick()}>Install app</div>
        );
    }
}