import * as React from 'react';
import { ToolSection } from '../framework/ToolSection';
import { db } from '../db/db';
import { getCurrentKey, generateRsaKey, importLifeKey, LifeKey } from '../db/crypt';
import * as dropboxApi from '../lib/dropboxApi';
import { setTheme, getVariable } from '../layout/theme';
import { getNoteTitle, getNoteTitleType, Note } from '../db/note';
import { Node } from '../components/TreeEditor'
import {createInstance} from 'localforage';
import moment from 'moment';
import { getAlias, mentionRegex } from '../db/person';
import { Api } from '../bridge/HostBridge';

const fileSystemInstance = createInstance({ name: 'filesystem' });
let dirHandle: FileSystemDirectoryHandle|null = null;
let perms: boolean|null = null;

fileSystemInstance.getItem('directory').then(async h => {
    dirHandle = h as FileSystemDirectoryHandle;
    if (!dirHandle) {
        return;
    }
    perms = await permissionStatus(dirHandle, true);
});

async function permissionStatus(fileHandle: FileSystemDirectoryHandle, readWrite: boolean) {
    const options: FileSystemHandlePermissionDescriptor = {};
    if (readWrite) {
        options.mode = 'readwrite';
    }
    // Check if permission was already granted. If so, return true.
    if ((await fileHandle.queryPermission(options)) === 'granted') {
        return true;
    }
    // The user didn't grant permission, so return false.
    return false;
}

export const SettingsPage: React.FC<{}> = () => {
    // const [pushSubscribed, setPushSubscribed] = React.useState<boolean|null>(null);
    const [currentKey, setCurrentKey] = React.useState<LifeKey|null>(null);

    const [serviceWorkerState, setServiceWorkerState] = React.useState('none');
    const [generatedKey, setGeneratedKey] = React.useState<string|null>(null);
    const [privateKey, setPrivateKey] = React.useState<string|null>(null);
    const [remoteConnected, setRemoteConnected] = React.useState<boolean|null>(null);
    const [blobConnectionString, setBlobConnectionString] = React.useState('');

    async function clearEntities() {
        if(!window.confirm('Clear all local data?')) {
            return;
        }

        db.contexts.dropInstance();
        db.tasks.dropInstance();
        db.notes.dropInstance();
        db.projects.dropInstance();
        db.people.dropInstance();
    }

    const [dropboxConnected, setDropboxConnected] = React.useState<boolean|null>(null);
    React.useEffect(() => {
        dropboxApi.isSignedIn().then(status => {
            setDropboxConnected(status);
        });
    }, [])

    React.useEffect(() => {
        async function check() {
            setRemoteConnected(await Api.syncRemoteConnected());
        }
        check();
    }, []);

    React.useEffect(() => {
        async function componentDidMount() {
            if(navigator.serviceWorker) {
                const registration = await navigator.serviceWorker.ready;
                const state = [registration.active ? 'active' : null, registration.waiting ? 'waiting' : null, registration.installing ? 'installing' : null];
                setServiceWorkerState(state.filter(x => x != null).join(', '))
            }
    
            await refreshKeys();
        }
        componentDidMount() 
    }, [])

    async function refreshKeys() {
        try {
            const newCurrentKey = await getCurrentKey();
            setCurrentKey(newCurrentKey)
        } catch(err) {
        }
    }

    function clearServiceWorker() {
        if (!navigator.serviceWorker) {
            return;
        }
        
        navigator.serviceWorker.ready.then(async (registration) => {
            await registration.update();
        });
    }


    function writeNode(node: Node, output: string, indent = ''): string {
        output += `${indent}- ${node.text}\n`;

        if (node.nodes) {
            for(const childNode of node.nodes) {
                output = writeNode(childNode, output, indent + '\t');
            }
        }

        return output;
    }

    async function formatContent(note: Note) {
        const people = await db.people.getAll();


        if(note.mode === 'tree' && note.content) {
            const nodes = JSON.parse(note.content) as Node[];
            let content = '';

            for(const node of nodes) {
                content = writeNode(node, content);
            }

            content = content.replace(mentionRegex, (_, group1) => {
                const person = people.find(x => `@${getAlias(x)}` === group1);

                if(person) {
                    return `[[${person.name}]]`;
                }

                return `[[Missing Person]]: ${group1}`;
            });

            return content;
        }

        return note.content;
    }
    
    async function exportToObsidian() {
        if (!dirHandle) {
            dirHandle = await window.showDirectoryPicker();
            fileSystemInstance.setItem('directory', dirHandle);
        }

        if(!await verifyPermission(dirHandle, true)) {
            perms = false;
            console.error('permission denied');
        }
        perms = true;
        
        const vaultHandle = await dirHandle.getDirectoryHandle("dump-test", { create: true });

        for(const note of await db.notes.getAll()) {
            if(note.noteFor) {
                continue;
            }

            const title = getNoteTitle(note);
            let fileName = `${title}.md`;
            let dir = vaultHandle;

            if (note.contextId) {
                const context = await db.contexts.get(note.contextId);
                if (context) {
                    // metadata.push(`context: ${context.title}`);
                    dir = await dir.getDirectoryHandle(context.title, { create: true });
                }
            }

            if(note.dailyDate) {
                dir = await dir.getDirectoryHandle('Daily', { create: true });
                fileName = `${moment(note.dailyDate).format('YYYY-MM-DD')}.md`;
            }

            console.log('writing ', fileName);
            const file = await dir.getFileHandle(fileName, { create: true });
            const writable = await file.createWritable({ keepExistingData: false });

            const metadata: string[] = [
            //     `id: ${note.id}`,
            //     `updated: ${note.updatedAt}`,
            //     `created: ${note.createdAt}`
            ];

            // if(note.mode) {
            //     metadata.push(`mode: ${note.mode}`);
            // }

            if (getNoteTitleType(note) === 'title') {
                metadata.push(`# ${note.title}`);
            }

            if (note.tags && note.tags.length) {
                metadata.push(`tags: ${note.tags.split(' ').map(t => `#${t}`).join(', ')}`);
            }

            if (metadata.length) {
                metadata.push('');
            }

            await writable.write(
`${metadata.join('\n')}
${await formatContent(note)}`);

            await writable.close();
        }

        const peopleHandle = await vaultHandle.getDirectoryHandle("People", { create: true });

        for(const person of await db.people.getAll()) {
            let fileName = `${person.name}.md`;

            console.log('writing ', fileName);
            const file = await peopleHandle.getFileHandle(fileName, { create: true });
            const writable = await file.createWritable({ keepExistingData: false });

            const metadata: string[] = [];

            if (person.birthday) {
                metadata.push(`birthday: ${moment(person.birthday).format(`YYYY-MM-DD`)}`);
            }

            if (metadata.length) {
                metadata.push('');
            }

            if (person.noteId) {
                const note = await db.notes.get(person.noteId);
                if(note) {
                    metadata.push(await formatContent(note))
                }
            }

            await writable.write(metadata.join('\n'));
            await writable.close();
        }
    }

    async function verifyPermission(fileHandle: FileSystemDirectoryHandle, readWrite: boolean) {
        const options: FileSystemHandlePermissionDescriptor = {};
        if (readWrite) {
            options.mode = 'readwrite';
        }
        // Check if permission was already granted. If so, return true.
        if ((await fileHandle.queryPermission(options)) === 'granted') {
            return true;
        }

        // Request permission. If the user grants permission, return true.
        if ((await fileHandle.requestPermission(options)) === 'granted') {
            return true;
        }
        // The user didn't grant permission, so return false.
        return false;
    }

    return (
        <>
            <ToolSection>
                <div className="button" onClick={() => setTheme('dark-theme')}>Dark theme</div>
                <div className="button" onClick={() => setTheme('light-theme')}>Light theme</div>
            </ToolSection>

            <ToolSection>
                <div className="button" onClick={() => window.location.reload()}>Reload</div>
                <div className="button" onClick={() => window.location.reload(true)}>Force Reload</div>
            </ToolSection>
            <ToolSection>
                <div className="button" onClick={exportToObsidian}>Export to Obsidian</div>
                {dirHandle && 
                    <div style={{display: 'inline-block', background: getVariable('--settings-info-background'), padding: '.5rem'}}>{dirHandle.name} - Access Status: {perms ? 'granted' : 'not granted'}</div>
                }
            </ToolSection>
            <ToolSection>
                {remoteConnected ? (
                    <div className="button" >Disconnect Blob Storage</div>
                ) : (
                    <>
                        <div className="button">Connect Blob Storage</div>
                        <input type="text" value={blobConnectionString} onChange={e => setBlobConnectionString(e.target.value)} />
                    </>
                )}
            </ToolSection>
            <ToolSection>
                {dropboxConnected ? (
                    <>
                        <div className="button" onClick={async () => { await dropboxApi.disconnect(); }}>Disconnect Dropbox</div>
                        <div className="button" onClick={async () => { await dropboxApi.refreshDropbox(); }}>Refresh Dropbox</div>
                    </>
                ) : (
                    <div className="button" onClick={async () => { await dropboxApi.signIn(); setDropboxConnected(await dropboxApi.isSignedIn()) }}>Connect Dropbox</div>
                )}
                <div style={{display: 'inline-block', background: getVariable('--settings-info-background'), padding: '.5rem'}}>Status: {dropboxConnected === null ? 'unknown' : dropboxConnected ? 'connected' : 'not connected'}</div>
                {dropboxApi.isLocalMode && <div style={{display: 'inline-block', background: getVariable('--settings-info-background'), padding: '.5rem'}}>Local [ {window.location.host} ]</div>}
                
            </ToolSection>
            <ToolSection>
                <div className="button" onClick={async () => await clearEntities()}>Clear Entities</div>
                <div className="button" onClick={() => clearServiceWorker()}>Update Service Worker</div>
                <div style={{display: 'inline-block', background: getVariable('--settings-info-background'), padding: '.5rem'}}>Service Workers [ {serviceWorkerState} ]</div>
            </ToolSection>
            {/* <ToolSection>
                {pushSubscribed === true && <div className="button" onClick={async () => await unsubscribePush()}>Unsubscribe Push</div>}
                {pushSubscribed === false && <div className="button" onClick={async () => await subscribePush()}>Subscribe Push</div>}
            </ToolSection> */}
            {false && <ToolSection>
                <h2>Encryption</h2>
                {!currentKey && (
                    <>
                        <div>
                            <textarea style={{width: '100%'}} onChange={e => setPrivateKey(e.target.value)} />
                        </div>
                        <button className="button" onClick={async () => { await importLifeKey(privateKey!); await refreshKeys(); }}>Set private key</button>
                        <h4>Generate key</h4>
                        <div className="button" onClick={async () => setGeneratedKey((await generateRsaKey()).privateKey)}>Generate</div>
                        <textarea style={{width: '100%', background: '#555', color: '#ddd'}} readOnly value={generatedKey || ''} />
                    </>
                )}
                <h4>Keys</h4>
                <div className="list">
                    {!currentKey && <div>No keys</div>}
                    {currentKey && (
                        <div className="item">
                            {currentKey!.fingerprint}
                        </div>
                    )}
                </div>
            </ToolSection>}
        </>
    );
}