import { db, ServerEntity } from "./db";
import * as _ from "lodash";
import { Data } from "../model";
import {EventListener} from './EventListener';
import * as dropboxApi from '../lib/dropboxApi';
import pLimit from 'p-limit';
import { Api } from "../bridge/HostBridge";

export enum SyncStatus {
    Idle,
    Syncing,
    Failed,
};

export interface SyncResult {
    startAt: Date;
    endAt: Date;
    type: 'desktop'|'dropbox';
    fetch: number;
    send: number;
    error: Error|null;
}

class Syncer {
    status = SyncStatus.Idle;
    results: SyncResult[] = [];
    data: Data|null = null;
    intervalHandle: number|null = null;

    syncListener = new EventListener<SyncStatus>();
    dataListener = new EventListener<Data>();

    constructor() {
        db.notes.changeListener.register(note => {
            if(!this.data) {
                return;
            }

            const notes = _.orderBy(this.data.notes.filter(x => x.id !== note.id).concat(note), x => x.updatedAt);
            this.data = new Data(notes, this.data.tasks, this.data.contexts, this.data.projects, this.data.people);
            this.dataListener.trigger(this.data);
            this.startServerSync();
        });

        db.tasks.changeListener.register(task => {
            if(!this.data) {
                return;
            }

            const tasks = this.data.tasks.filter(x => x.id !== task.id).concat(task);
            this.data = new Data(this.data.notes, tasks, this.data.contexts, this.data.projects, this.data.people);
            this.dataListener.trigger(this.data);
            this.startServerSync();
        });

        db.contexts.changeListener.register(context => {
            if(!this.data) {
                return;
            }

            const contexts = this.data.contexts.filter(x => x.id !== context.id).concat(context);
            this.data = new Data(this.data.notes, this.data.tasks, contexts, this.data.projects, this.data.people);
            this.dataListener.trigger(this.data);
            this.startServerSync();
        });

        db.projects.changeListener.register(project => {
            if(!this.data) {
                return;
            }

            const projects = this.data.projects.filter(x => x.id !== project.id).concat(project);
            this.data = new Data(this.data.notes, this.data.tasks, this.data.contexts, projects, this.data.people);
            this.dataListener.trigger(this.data);
            this.startServerSync();
        });

        db.people.changeListener.register(person => {
            if(!this.data) {
                return;
            }

            const people = this.data.people.filter(x => x.id !== person.id).concat(person);
            this.data = new Data(this.data.notes, this.data.tasks, this.data.contexts, this.data.projects, people);
            this.dataListener.trigger(this.data);
            this.startServerSync();
        });
    }

    registerSyncListener(listener: (status: SyncStatus) => void) {
        this.syncListener.register(listener)
    }

    registerDataListener(listener: (status: Data) => void) {
        this.dataListener.register(listener)
    }

    getResults() {
        return this.results;
    }

    async getData() {
        if (!this.data) {
            this.data = await this.loadData();
        }

        return this.data;
    }

    async ensureServerSyncEnqueued() {
        if (this.intervalHandle !== null) {
            return;
        }

        this.intervalHandle = setInterval(async () => {
            await this.startServerSync();
        }, 60000) as any;
    }

    async startServerSync() {
        // console.trace('sync');

        if (this.status === SyncStatus.Syncing) {
            return;
        }

        if(await dropboxApi.isSignedIn()) {
            await this.runDropboxSync();
        }

        await this.runBridgeSync();
    }
    
    async runBridgeSync() {
        if(!Api.hasBridge()) {
            return;
        }

        const startAt = new Date();

        function difference<T>(setA: Set<T>, setB: Set<T>): Set<T> {
            let _difference = new Set(setA);
            for (const key of setB) {
                _difference.delete(key);
            }
            return _difference;
        }

        try {
            await Api.syncRemote();

            const serverKeys = await Api.getSyncKeys();
            const syncKeys = new Set(await db.getSyncKeys());
            
            const toSend = difference(syncKeys, new Set(serverKeys));
            const toFetch = difference(new Set(serverKeys), syncKeys);

            if (toSend.size || toFetch.size) {
                this.status = SyncStatus.Syncing;
                this.syncListener.trigger(this.status);
            }
    
            for(const key of toSend) {
                const entities = await db.exportServerEntities([key]);
                await Api.sendEntity(key, JSON.stringify(entities[0]));
            }
    
            for(const key of toFetch) {
                const contents = await Api.getEntity(key);
                const entity: ServerEntity = JSON.parse(contents);
                await db.importServerEntites([entity]);
            }

            if(toFetch.size > 0) {
                const data = await this.loadData();
                this.dataListener.trigger(data);
            }

            if (toSend.size > 0) {
                await Api.syncRemote();
            }

            this.results.push({ type: 'desktop', startAt, endAt: new Date(), error: null, fetch: toFetch.size, send: toSend.size })
            this.status = SyncStatus.Idle;
            this.syncListener.trigger(this.status);            
        } catch (e) {
            this.status = SyncStatus.Failed;
            this.results.push({ type: 'desktop', startAt, endAt: new Date(), error: e && e.message ? e.message : e, fetch: 0, send: 0 });
            this.syncListener.trigger(this.status);
        }
    }

    async runDropboxSync() {
        const startAt = new Date();
    
        function difference<T>(setA: Set<T>, setB: Set<T>): Set<T> {
            let _difference = new Set(setA);
            for (const key of setB) {
                _difference.delete(key);
            }
            return _difference;
        }
    
        try {
            const files = await dropboxApi.listFiles();
            const syncKeys = new Set(await db.getSyncKeys());
    
            const toSend = difference(syncKeys, files);
            const toFetch = difference(files, syncKeys);

            if (toSend.size || toFetch.size) {
                this.status = SyncStatus.Syncing;
                this.syncListener.trigger(this.status);
            }            
    
            for(const key of toSend) {
                const entities = await db.exportServerEntities([key]);
                await dropboxApi.uploadFile(key, JSON.stringify(entities[0]));
            }
    
            const limit = pLimit(10);
            const promises = [];
    
            for(const key of toFetch) {
                promises.push(limit(async () => {
                    const entity = await dropboxApi.getFile<ServerEntity>(key);
                    await db.importServerEntites([entity]);
                }));
            }
    
            await Promise.all(promises);
    
            if(toFetch.size > 0) {
                const data = await this.loadData();
                this.dataListener.trigger(data);
            }
    
            this.results.push({ type: 'dropbox', startAt, endAt: new Date(), error: null, fetch: toFetch.size, send: toSend.size })
            this.status = SyncStatus.Idle;
            this.syncListener.trigger(this.status);
        } catch (e) {
            this.status = SyncStatus.Failed;
            this.results.push({ type: 'dropbox', startAt, endAt: new Date(), error: e && e.message ? e.message : e, fetch: 0, send: 0 });
            this.syncListener.trigger(this.status);
        }
    }

    private async loadData(): Promise<Data> {
        const contexts = db.contexts.getAll();
        const tasks = await db.tasks.getAll();
        const projects = db.projects.getAll();
        const people = db.people.getAll();
        const notes = _.orderBy(await db.notes.getAll(), x => x.updatedAt);
        
        return new Data(notes, await tasks, await contexts, await projects, await people);
    }
}

export const syncer = new Syncer();



