import React from "react";
import { Context } from "../db/context";
import { Note } from "../db/note";

function getBridge() {
    const desktop: HostBridge = (window as any).chrome?.webview?.hostObjects?.dotnet;
    const android: HostBridge = (window as any).AndroidBridge;
    return desktop || android || null;
}

function hasBridge() {
    return !!getBridge();
}

interface HostBridge {
    getNote(id: string): Promise<string>;
    getContext(id: string): Promise<string>;
    getEntity(key: string): Promise<string>;
    sendEntity(key: string, contents: string): Promise<void>;
    getSyncKeys(): Promise<string>;
    getNotes(): Promise<string>
    syncRemote(): Promise<void>
    syncRemoteConnected(): Promise<boolean>
    disconnectBlobStorage(): Promise<void>
    connectBlobStorage(connectionString: String): Promise<void>
}

function getEntity(key: string) {
    return getBridge().getEntity(key);
}

function sendEntity(key: string, contents: string) {
    return getBridge().sendEntity(key, contents);
}

async function getSyncKeys() {
    return JSON.parse(await getBridge().getSyncKeys()) as string[];
}

async function getNotes() {
    return JSON.parse(await getBridge().getNotes()) as Note[];
}

async function getNote(id: string) {
    return JSON.parse(await getBridge().getNote(id)) as Note;
}

async function getContext(id: string) {
    return JSON.parse(await getBridge().getContext(id)) as Context;
}

async function syncRemote() {
    await getBridge().syncRemote();
}

async function syncRemoteConnected() {
    return await getBridge().syncRemoteConnected();
}

