export const useNotes = () => {
    const [data, setData] = React.useState<Note[]|null>(null);
  
    async function fetchData() {
        console.time('getNotes');
        const response = await Api.getNotes();
        console.timeEnd('getNotes');
        setData(response);
    }
  
    React.useEffect(() => {fetchData()}, []);
    return data;
};

export const useNote = (id: string|null) => {
    const [data, setData] = React.useState<Note|null>(null);
  
    async function fetchData(id: string|null) {
        if(!id) {
            return;
        }

        console.time('getNote');
        const response = await Api.getNote(id);
        console.timeEnd('getNote');
        setData(response);
    }
  
    React.useEffect(() => {fetchData(id)}, [id]);
    return data;
};

export const useContext = (id: string|null) => {
    const [data, setData] = React.useState<Context|null>(null);
  
    async function fetchData(id: string|null) {
        if(!id) {
            return;
        }

        // console.time('getContext');
        const response = await Api.getContext(id);
        // console.timeEnd('getContext');
        setData(response);
    }
  
    React.useEffect(() => {fetchData(id)}, [id]);
    return data;
};

export const Api = {
    hasBridge,
    getEntity,
    sendEntity,
    getSyncKeys,
    getNotes,
    getNote,
    getContext,
    syncRemote,
    syncRemoteConnected,
};

